@media screen and (min-width: 300px) and (max-width: 385px) {

    .App {

        
        
        .letter {
            width: 31px;

            &.enter {
                text-align: center;
                width: fit-content;
            }

            &.backspace {
                width: fit-content;
            }
        }
    }

}


@media screen and (min-width: 385px) and (max-width: 417px) {

    .App {

        
        
        .letter {
            width: 34px;

            &.enter {
                text-align: center;
                width: fit-content;
            }

            &.backspace {
                width: 50px;
            }
        }
    }

}

@media screen and (min-width: 417px) and (max-width: 480px) {

    .App {        
        max-width: 100vw;

        .row {
            margin: 7px 0;
            
            .letter {
                width: 35px;
                height: 55px;
                font-size: 12px;
                
                &.enter {
                    text-align: center;
                    width: fit-content;
                }

                &.backspace {
                    width: fit-content;
                }
            }
        }
        
    }
}