@import "./colors";
@import "./mixins";
@import "./effects";

.wordle {
    display: flex;
    flex-direction: column;
    // margin-top: 10px;
    width: 320px;
    // background-color: aqua;
    align-items: center;
    padding: 7px 0;
    position: relative;
    

    .message {
        display: flex;
        background-color: #ffffff;
        width: auto;
        height: auto;
        position: absolute;
        top: 12px;
        padding: 12px 12px;
        border-radius: 5px;
        color: $background;
        font-size: 12px;
    }

      

    // [class^="row-"] {
        .row{
        display: flex;        
        justify-content: space-between;
        margin: 5px 0;
        

        .box {
            border: 2px solid $border;
            width: 55px;
            height: 55px;
            margin: 0 3px;
            font-size: 30px;
            font-weight: 700;
            display: flex;
            align-items: center;
            justify-content: center;
            text-transform: uppercase;

            &.yellow {
                @include yellow;
                border: 2px solid $yellow;
            
            }

            &.green {
                @include green;
                border: 2px solid $green;
            }
            &.inactive {
                @include inactive;
                border: 2px solid $inactive;
                
            }
            &.active {
                @include active
            } 
        }
    }
}

.keyboard {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin-top: 3px;

    .row {
        display: flex;
        justify-content: space-between;
        margin: 7px 0;
        height: 50px;
        
    }

    .letter {
    background-color: $active;
    outline: none;
    color: #ffffff;
    border-radius: 5px;
    width: 40px;
    height: 58px;
    border: none;
    margin: 0 3px;
    font-weight: 700;
    cursor: pointer;

    &.enter {
        width: 70px;
    }
    &.backspace {
        width: 50px;
    }

    &.yellow {
        @include yellow
    }
    &.green {
        @include green
    }
    &.inactive {
        @include inactive
    }


    
    }
}

.hide {
    display: none;
}