.header {
  background-color: #121213;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  border: 2px solid #3a3a3c;
}
.header .left {
  margin-left: 20px;
  display: flex;
}
.header .left .icon {
  margin-right: 5px;
}
.header .right {
  display: flex;
  flex-direction: row;
  margin-right: 20px;
}
.header .right .icon {
  margin-left: 5px;
}
.header .center {
  font-family: "Newsreader", serif;
  font-weight: 800;
  font-size: 30px;
}

.alert {
  -webkit-animation: shake-horizontal 0.8s cubic-bezier(0.455, 0.03, 0.515, 0.955) both;
  animation: shake-horizontal 0.8s cubic-bezier(0.455, 0.03, 0.515, 0.955) both;
}

@-webkit-keyframes shake-horizontal {
  0%, 100% {
    transform: translateX(0);
  }
  10%, 30%, 50%, 70% {
    transform: translateX(-10px);
  }
  20%, 40%, 60% {
    transform: translateX(10px);
  }
  80% {
    transform: translateX(8px);
  }
  90% {
    transform: translateX(-8px);
  }
}
@keyframes shake-horizontal {
  0%, 100% {
    transform: translateX(0);
  }
  10%, 30%, 50%, 70% {
    transform: translateX(-10px);
  }
  20%, 40%, 60% {
    transform: translateX(10px);
  }
  80% {
    transform: translateX(8px);
  }
  90% {
    transform: translateX(-8px);
  }
}
.fade-out {
  -webkit-animation: fade-out 3s ease-out both;
  animation: fade-out 3s ease-out both;
}

@-webkit-keyframes fade-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes fade-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.wordle {
  display: flex;
  flex-direction: column;
  width: 320px;
  align-items: center;
  padding: 7px 0;
  position: relative;
}
.wordle .message {
  display: flex;
  background-color: #ffffff;
  width: auto;
  height: auto;
  position: absolute;
  top: 12px;
  padding: 12px 12px;
  border-radius: 5px;
  color: #121213;
  font-size: 12px;
}
.wordle .row {
  display: flex;
  justify-content: space-between;
  margin: 5px 0;
}
.wordle .row .box {
  border: 2px solid #3a3a3c;
  width: 55px;
  height: 55px;
  margin: 0 3px;
  font-size: 30px;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
}
.wordle .row .box.yellow {
  background-color: #b59f3b;
  border: 2px solid #b59f3b;
}
.wordle .row .box.green {
  background-color: #538d4e;
  border: 2px solid #538d4e;
}
.wordle .row .box.inactive {
  background-color: #3a3a3c;
  border: 2px solid #3a3a3c;
}
.wordle .row .box.active {
  border: 2px solid #565758;
}

.keyboard {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-top: 3px;
}
.keyboard .row {
  display: flex;
  justify-content: space-between;
  margin: 7px 0;
  height: 50px;
}
.keyboard .letter {
  background-color: #818384;
  outline: none;
  color: #ffffff;
  border-radius: 5px;
  width: 40px;
  height: 58px;
  border: none;
  margin: 0 3px;
  font-weight: 700;
  cursor: pointer;
}
.keyboard .letter.enter {
  width: 70px;
}
.keyboard .letter.backspace {
  width: 50px;
}
.keyboard .letter.yellow {
  background-color: #b59f3b;
}
.keyboard .letter.green {
  background-color: #538d4e;
}
.keyboard .letter.inactive {
  background-color: #3a3a3c;
}

.hide {
  display: none;
}

@media screen and (min-width: 300px) and (max-width: 385px) {
  .App .letter {
    width: 31px;
  }
  .App .letter.enter {
    text-align: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }
  .App .letter.backspace {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }
}
@media screen and (min-width: 385px) and (max-width: 417px) {
  .App .letter {
    width: 34px;
  }
  .App .letter.enter {
    text-align: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }
  .App .letter.backspace {
    width: 50px;
  }
}
@media screen and (min-width: 417px) and (max-width: 480px) {
  .App {
    max-width: 100vw;
  }
  .App .row {
    margin: 7px 0;
  }
  .App .row .letter {
    width: 35px;
    height: 55px;
    font-size: 12px;
  }
  .App .row .letter.enter {
    text-align: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }
  .App .row .letter.backspace {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }
}
.App {
  background-color: #121213;
  height: 100vh;
  color: #ffffff;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  align-items: center;
  flex-direction: column;
  overflow: hidden;
}/*# sourceMappingURL=style.css.map */