@import "./colors";

.header {
    background-color: $background;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 50px;
    border: 2px solid $border;

    .left {
        margin-left: 20px;
        display: flex;

        .icon {
            margin-right: 5px;
        }
    }

    .right {
        
        display: flex;
        flex-direction: row;
        margin-right: 20px;
        .icon {
            margin-left: 5px;
        }

    }
    .center {
        font-family: 'Newsreader', serif;
        font-weight: 800;
        font-size: 30px;
    }
}